<template>
<div style="height:100%;background:#FFFFFF;width:100%;">
  <div class="xwjTitle" v-if="this.ifShowTop">
      <div class="xwjLogo">
        <img :src="icon.xwjlogo">
      </div>
      <div class="xwj">
        <span class="xwjTxt">信我家</span>
        <span class="xwjDesc">海信智慧社区</span>
      </div>
      <div class="toAPP">
        <button class="openAPP" @click='openApp'>打开APP</button>
      </div>
    </div>
  <div class="pageView">
    <div style="color:#999999;margin-bottom:10px;">验证码:6位数字</div>
    <div>
      <van-field v-model="msgCode" placeholder="请输入验证码">
        <van-button
          slot="button"
          size="small"
          @click="sendMsgCode"
          :disabled="!btnEnable"
        >{{msgBtnText}}</van-button>
      </van-field>
    </div>
    <div class="bottomView">
      <div class="nextBtn" @click="commitAction">确 认</div>
    </div>
  </div>
</div>
</template>

<script>
import Vue from 'vue';
import { Field } from 'vant';
import { Button } from 'vant';

Vue.use(Button);
Vue.use(Field);
import { approvalIdenti, getMsgCode } from "../../service/verifyApplyService";
export default {
  name: "CommitPage",
  data() {
    return {
      msgCode: "",
      uuid: "",
      phone: "",
      resultType: "",
      btnEnable: true,
      msgBtnText: "发送验证码",
      icon:{
         xwjlogo:require("../../assets/share/xinwojiaLogo36@2x.png")
      }
    };
  },
  methods: {
    sendMsgCode() {
      let param = { PHONE: this.phone };
      getMsgCode(param)
        .then(data => {
          this.$toast(data.msg);
          this.timeCountDown();
        })
        .catch(error => {
          console.log(error);
          // this.$toast(error);
        });
    },
    timeCountDown() {
      this.btnEnable = false;
      var timeNumber = 60;
      let clock = window.setInterval(() => {
        timeNumber--;
        if (timeNumber > 0) {
          this.msgBtnText = timeNumber + "秒后重新发送";
        } else {
          this.btnEnable = true;
          window.clearInterval(clock);
          this.msgBtnText = "获取验证码";
        }
      }, 1000);
    },
    commitAction() {
      if (this.msgCode == "") {
        this.$toast("验证码不能为空");
        return;
      }
      let param = {
        uuid: this.uuid,
        phone: this.phone,
        verificationCode: this.msgCode,
        approvalResult: this.resultType
      };
      approvalIdenti(param)
        .then(data => {
          console.log(data);
          this.parasData(data);
        })
        .catch(error => {
          console.log(error);
          // this.$toast(error);
        });
    },
    parasData(data) {
      let userData = JSON.parse(this.$route.query.data);
      let roomStr = "【"+userData.areaName + '-' + userData.buildName + '-' + userData.showRoomName+"】";
      let param = {
        nickName: userData.ownerNickName,
        room: roomStr,
        type:this.$route.query.type
      };
      console.log(this.$route.query)
      console.log(param)
      this.$router.push({ path: "/resultPage", query: param });
    },
    openApp(){
      this.shareOpenApp('myfamily')
   }
  },
  mounted() {
    let query = this.$route.query;
    let dataObj = JSON.parse(query.data)
    console.log(dataObj);
    this.uuid = dataObj.uuid;
    this.phone = query.phone;
    this.resultType = query.type;
  }
};
</script>

